/* Some keyframe animations here */
.rotation-animation {
  transition: all ease;
  -webkit-animation: rotation 100s infinite linear 1s, roto2 0.8s linear;
  animation: rotation 100s infinite linear 1s, roto2 0.8s linear;
}
@-webkit-keyframes roto2 {
  0% {
    width: 0%;
  }
  100% {
    width: 100%;
  }
}
@keyframes roto2 {
  0% {
    width: 0%;
  }
  100% {
    width: 100%;
  }
}

@-webkit-keyframes rotation {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }

  100% {
    opacity: 1;
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

@keyframes rotation {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }

  100% {
    opacity: 1;
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
@-webkit-keyframes roto {
  0% {
    width: 0%;
    opacity: 0;
  }
  100% {
    width: 30%;
    opacity: 1;
  }
}
@keyframes roto {
  0% {
    width: 0%;
    opacity: 0;
  }
  100% {
    width: 30%;
    opacity: 1;
  }
}

span.my-tooltip {
  position: absolute;
  opacity: 0;
  background: #4b42ad 0% 0% no-repeat padding-box;
  box-shadow: 0 0.1875rem 2.1875rem #00000026;
  border-radius: 0.375rem;
  font: normal normal normal 1.125rem/1.5625rem Yellix TRIAL;
  letter-spacing: -0.01375rem;
  color: #ffffff;
  padding: 0.625rem;
  height: 3.125rem;
  width: 12.5rem;
  left: 50%;
  top: 0;
  text-align: center;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}

span.my-tooltip:nth-child(1) {
  opacity: 1;
}

span.my-tooltip::after {
  content: "";
  position: absolute;
  top: 100%; /* At the bottom of the tooltip */
  left: 50%;
  margin-left: -0.3125rem;
  border-width: 0.3125rem;
  border-style: solid;
  border-color: #4b42ad transparent transparent transparent;
}

.tooltip-wrapper:hover .my-tooltip {
  opacity: 1;
}
